.mainContainer {
  position: absolute;
  width: 100%;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 0.8em;
  color: rgb(126, 126, 126);
  z-index: 1;
  top: 200px;
  padding: 20px;
  overflow-x: hidden;
}

.mainTopPost ul {
  position: relative;
  list-style: none;
  width: 70%;
  height: 100%;
  display: flex;
  padding: 0;
  left: 50%;
  transform: translate(-50%);
}
.mainTopPost li {
  position: relative;
  width: 100%;
  margin: 15px;
  margin-bottom: 10px;
}
.mainTopPost li:hover {
  font-weight: normal;
  color: rgba(0, 0, 0, 0.39);
}
.mainTopImg {
  position: relative;
  width: 100%;
  height: 50%;
}

@media screen and (max-width: 1023px) {
  .mainTopPost ul {
    width: 100%;
    display: block;
    left: 60%;
  }
  .mainTopPost li {
    width: 320px;
    float: left;
    margin-bottom: 10px;
  }
  .mainTopImg {
    width: 100%;
    height: 250px;
  }
}

.mainTopPost p {
  margin-top: 10px;
  line-height: 200%;
}
.mainTopTitle {
  text-decoration: underline;
}
.mainTopText {
  font-size: 1.1em;
  color: rgb(110, 110, 110);
  font-weight: 900;
}

.slider {
  position: relative;
  margin-top: 50px;
  width: 100%;
  height: 420px;
  background-color: rgba(0, 0, 0, 0.411); /*test*/
  text-align: center;
  display: flex;
}
@media screen and (max-width: 1023px) {
  .slider {
    margin-top: 850px;
  }
}
.slider div {
  position: relative;
  flex: 0 0 auto;
  width: 100%;
  height: 100%;
  background-size: cover;
  z-index: 0;
}
.sliding {
  transition: all 1s;
  transform: translate(-100%);
}
.slidingBack {
  transition: all 1s;
  transform: translate(0%);
}

#sliderImg1 {
  background-image: url(../images/study/main_etc.jpg);
}
#sliderImg2 {
  background-image: url(https://img.freepik.com/premium-photo/books-on-shelves-in-the-library-library-interior_124603-3226.jpg);
}

.sliderTitle {
  font-size: 3em;
  position: absolute;
  top: 200px;
  transform: translate(-50%, -50%);
  color: white;
  text-shadow: 2px 2px 2px gray;
}
.sliderTitle:hover {
  color: rgba(255, 255, 255, 0.548);
}

.slider .sliderButton {
  position: absolute;
  border: 1px solid white;
  bottom: 0px;
  width: 120px;
  height: 50px;
  top: 250px;
  color: rgb(255, 255, 255);
  transform: translate(-80%, -30%);
  font-size: 10pt !important;
  text-shadow: 2px 2px 2px gray;
}
.slider .sliderButton:hover {
  color: rgba(255, 255, 255, 0.548);
}
.sliderControl {
  position: relative;
  top: -260px;
  width: 100%;
  height: 100px;
  z-index: 1;
}
.sliderControl #prev {
  position: absolute;
  left: 0px;
  content: "";
  width: 50px; /* 사이즈 */
  height: 50px; /* 사이즈 */
  border-top: 3.5px solid #ffffff; /* 선 두께 */
  border-right: 3.5px solid #ffffff; /* 선 두께 */
  transform: rotate(225deg); /* 각도 */
}
.sliderControl #next {
  position: absolute;
  right: 0px;
  content: "";
  width: 50px; /* 사이즈 */
  height: 50px; /* 사이즈 */
  border-top: 3.5px solid white; /* 선 두께 */
  border-right: 3.5px solid white; /* 선 두께 */
  transform: rotate(45deg); /* 각도 */
}

.mainCard {
  position: relative;
}
.cardBox {
  position: relative;
  width: 80%;
  height: 230px;
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px,
    rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px,
    rgba(0, 0, 0, 0.07) 0px 16px 16px;
  margin-bottom: 80px;
  left: 50%;
  transform: translate(-50%);
  overflow-y: scroll;
}
.cardBox:nth-child(4n) {
  direction: rtl;
}

.mainCard img:nth-child(4n + 1) {
  position: absolute;
  width: 250px;
  height: 230px;
  left: 10%;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  opacity: 90%;
}
.cardBox div {
  position: relative;
  padding: 50px;
  padding-left: 290px;
  text-align: center;
  font-size: 1.2em;
}

.mainCard div:nth-child(4n) div {
  position: relative;
  padding: 50px;
  padding-right: 290px;
  text-align: center;
}
.mainCard img:nth-child(3n) {
  position: absolute;
  width: 250px;
  height: 230px;
  right: 10%;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  opacity: 90%;
}
