.post {
  position: absolute;
  width: 65%;
  margin-top: 250px;
  padding-bottom: 250px;
  font-family: "Noto Sans KR", sans-serif;
  left: 15%;
  transform: translate(-15%);
}
.projectPost {
  position: relative;
  width: 60%;
  padding-top: 250px;
  padding-bottom: 150px;
  font-family: "Noto Sans KR", sans-serif;
  left: 50%;
  transform: translate(-50%);
}
.post img,
.projectPost img {
  width: 100%;
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
#gif {
  width: 20%;
  margin: 10px;
  position: relative;
  float: right;
  margin-left: 50px;
}
.miniImg {
  width: 50% !important;
  margin: 20px;
}

.postText,
.projectPost img {
  font-size: 10pt;
  margin-top: 25px;
  margin-bottom: 25px;
}
@media screen and (min-width: 1023px) {
  .post {
    width: 52%;
  }
  .postText {
    font-size: 13pt;
  }

  .projectPost {
    width: 50%;
  }
}

.list {
  list-style: decimal;
  line-height: 2em;
}
.postTag ul {
  list-style: none;
}
.postTag li {
  float: left;
  margin-left: 12px;
  font-size: 0.8em;
  color: rgb(112, 112, 112);
  background-color: #d6f0f1;
  border-radius: 20px;
  padding: 2px 6px 2px 6px;
}
.postTag li:nth-child(even) {
  background-color: #f3eed1;
}

mark {
  background-color: #d6f0f1;
}
mark.y {
  background-color: #f8f8d9;
}

.textColor {
  color: cadetblue;
}
